// src/routes.js

import React from 'react';
import { Route, BrowserRouter } from 'react-router-dom';
import App from './App';
import Recommendations from './components/Recommendations'

import history from './history';

export const makeMainRoutes = () => {
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL} history={history} component={App}>
      <div>    
        <Route exact path="/" render={(props) => <Recommendations {...props} />} />    
        <Route path="/?title=:title" render={(props) => <Recommendations {...props} />}/>           
      </div>
    </BrowserRouter>
  );
}