import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import Slider from "react-slick";
import './RecommendationsSlider.css';


const RightArrow = () => {
  // <button className="arrow fas fa-chevron-right"></button>
  return (
    <button type="button" data-role="none" className="slick-arrow slick-next" style={{display: "block"}}>
      <i className="slick-arrow slick-next fa fa-chevron-right next"></i>    
    </button>
  );
}

class RecommendationsSlider extends React.Component {
    constructor(props, context) {
      super(props, context);
  
      this.handleSelect = this.handleSelect.bind(this);
  
      this.state = {
        index: 0,
        direction: null
      };
    }
  
    handleSelect(selectedIndex, e) {
      alert(`selected=${selectedIndex}, direction=${e.direction}`);
      this.setState({
        index: selectedIndex,
        direction: e.direction
      });
    }
  
    render() {
      const { index, direction } = this.state;
              
      var settings = {
        
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 5,
        slidesToScroll: 5,
        accessibility: true,
        adaptiveHeight: true,
        arrows: true,
        responsive: [
          {
            breakpoint: 1024,
            settings: {
              slidesToShow: 3,
              slidesToScroll: 3,
              infinite: true,
              dots: true
            }
          },
          {
            breakpoint: 600,
            settings: {
              slidesToShow: 2,
              slidesToScroll: 2,
              initialSlide: 2
            }
          },
          {
            breakpoint: 480,
            settings: {
              slidesToShow: 1,
              slidesToScroll: 1
            }
          }
        ]
      };

      return (        
        <div className="rec-slider">
          {this.props.recommendations.length > 0 &&            
            <div className="rec-title">Recommended</div>           
          } 
            {this.props.recommendations.length > 0 &&                  
                <Slider {...settings}>
                    {this.props.recommendations.map((recommendation, i) => ( 
                        <a href={"https://www.universcine.de/de_DE/films/"+recommendation.slug_recommended} 
                          target="_parent"
                          key={i}>
                          <div >
                              <img className="img img-responsive" 
                                  height={350.92} 
                                  width="100%"
                                  alt="300x200" 
                                  src={recommendation.metadata.poster_url} 
                                  className="film-img"
                              />
                          </div>
                        </a>
                                                             
                    ))}
                </Slider>                     

            }
        </div>
    
      );
    }
  }
  

  export default RecommendationsSlider;