import React from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import RecommendationsSlider from './RecommendationsSlider';

const qs = require('query-string');

var slugify = require('slugify')

const getFromURL = (url) => {
    var xmlHttp = new XMLHttpRequest();
    xmlHttp.open( "GET", url, false );
    xmlHttp.send( null );
    var response = xmlHttp.responseText;
    var results = JSON.parse(response);

    return results;
}

const getRecommendations = (slug) => {

    var url = baseURL + "recommendations?slug=" + slug;
    console.log(url);

    return getFromURL(url);

}

const getTitle = (searchPath) => {        
    var params = qs.parse(searchPath);
    
    return params.title;
}

const baseURL = 'https://ryskn4s5c5.execute-api.eu-west-1.amazonaws.com/v1/';


class Recommendations extends React.Component {
    constructor(props) {
        console.log(props);
        super(props);
        this.state = {   
            //title:this.props.match.params.title,
            title: getTitle(props.location.search),
            slug: '',
            recommendations: [],            
        };        
        
        console.log(props);
    }

    componentWillMount(props) {        
        var title = this.state.title;
        var slug = slugify(this.state.title,
            {
                lower: true  

            });
        var recommendations = getRecommendations(slug);

        console.log(recommendations);

        this.setState({
            title:title,
            slug: slug,
            recommendations: recommendations
        }, () => {
            console.log(this.state)
        })
    }    

    render() {
        
        return (
            <div style={{padding: "20px"}}> 
                <RecommendationsSlider 
                    recommendations = {this.state.recommendations}
                />      
                
            </div>
        );
    }
}

export default Recommendations;